import React, { useState } from "react";
import {
  createTable,
  useTableInstance,
  getCoreRowModel,
  getExpandedRowModel,
} from "@tanstack/react-table";
import './basic-table.css';
import STUDENTS from "../students.json";
import smallestChild from '../asset/img/dynamic.png';
import expandAll from '../asset/img/maximize (2).png';
import collapseAll from '../asset/img/up-and-down-arrow.png';
import expandItem from '../asset/img/arrow-r.png';
import collapseItem from '../asset/img/arrow-d.png';

const table = createTable();

const levelIconHeaderStyle = {
  verticalAlign: "middle",
  marginTop: "-2px",
  width: 25.5,
  height: 25.5,
  cursor: "pointer",
};

const levelIconStyle = {
  verticalAlign: "middle",
  marginBottom: "3px",
  width: 20,
  height: 20,
  cursor: "pointer",
};

const levelIconFinalLevelStyle = {
  verticalAlign: "middle",
  marginTop: "3px",
  width: 15,
  height: 15,
  cursor: "not-allowed",
};

const BasicTable = (props) => {

  console.log("***** defaultColumnsArr *****");
  const defaultColumnsArr = props.defaultColumnsArr;
  console.log(defaultColumnsArr);

  // Add first col header
  const defaultColumns = [
    table.createDataColumn(defaultColumnsArr[0], {
      id: defaultColumnsArr[0],
      header: (props) => (
        <div style={{display: "flex", marginLeft: "8px"}}>
          <button onClick={props.instance.getToggleAllRowsExpandedHandler()}>
            {props.instance.getIsAllRowsExpanded() ? 
            // ""
            <img src={collapseAll} alt="collapseAll" style={levelIconHeaderStyle}></img>
            :
            // ""
            <img src={expandAll} alt="expandAll" style={levelIconHeaderStyle}></img>
            }
          </button>
          <span style={{paddingLeft: "6px"}}>{defaultColumnsArr[0]}</span>
        </div>
      ),

      cell: ({ row, getValue }) => (
        <div
          style={{
            // Since rows are flattened by default,
            // we can use the row.depth property
            // and paddingLeft to visually indicate the depth
            // of the row
            // backgroundColor: COLORS[row.depth],
            paddingLeft: `${row.depth * 2}rem`,
            // paddingLeft: `${row.depth * 1}rem`,
            display: "flex",
          }}
        >
          {row.getCanExpand() ? (
            <button
              {...{
                onClick: row.getToggleExpandedHandler(),
                style: { cursor: "pointer" },
              }}
            >
              {
                // row.getIsExpanded() ? "" : ""
                row.getIsExpanded() ? 
                <img src={collapseItem} alt="collapseItem" style={levelIconStyle}></img>
                : 
                <img src={expandItem} alt="expandItem" style={levelIconStyle}></img>
              }
            </button>
          ) : (
            // ""
            // ""
            <img src={smallestChild} alt="final-level" style={levelIconFinalLevelStyle}></img>
          )}
          <span style={{paddingLeft: "6px"}}>{getValue()}</span>
        </div>
      ),
    })]

  // Add remain cols header
  for (var i = 1; i < defaultColumnsArr.length; i++) {
    defaultColumns.push(
      table.createDataColumn(defaultColumnsArr[i], {
        id: defaultColumnsArr[i],
      }));
  }
  // console.log("defaultColumns: " + defaultColumns);
  const defaultDataJson = props.defaultDataJson;
  const defaultData = [...defaultDataJson];
  console.log("defaultData: " + defaultData);

  const defaultData1 = [...STUDENTS.slice(0, 5)];
  console.log("defaultData1: " + defaultData1);

  const [data, setData] = useState([...defaultData]);
  const [columns, setColumns] = useState([...defaultColumns]);
  const [expanded, setExpanded] = useState({});
  const instance = useTableInstance(table, {
    data,
    columns,
    state: {
      expanded: expanded,
    },
    onExpandedChange: setExpanded,
    getSubRows: (row) => row.subRows,
    getCoreRowModel: getCoreRowModel(),
    getExpandedRowModel: getExpandedRowModel(),
  });
  console.log(instance.getRowModel());
  return (
    <div>
      <table border={1}>
        <thead>
          {instance.getHeaderGroups().map((headerGroup) => (
            <tr key={headerGroup.id}>
              {headerGroup.headers.map((header) => (
                <th key={header.id} colSpan={header.colSpan}>
                  {header.isPlaceholder ? null : header.renderHeader()}
                </th>
              ))}
            </tr>
          ))}
        </thead>
        <tbody>
          {instance.getRowModel().rows.map((row) => (
            <tr key={row.id} className={`depth-${row.depth}`}>
              {row.getVisibleCells().map((cell) => (
                <td key={cell.id}>{cell.renderCell()}</td>
              ))}
            </tr>
          ))}
        </tbody>
        <tfoot>
          {instance.getFooterGroups().map((footerGroup) => (
            <tr key={footerGroup.id}>
              {footerGroup.headers.map((header) => (
                <th key={header.id} colSpan={header.colSpan}>
                  {header.isPlaceholder ? null : header.renderFooter()}
                </th>
              ))}
            </tr>
          ))}
        </tfoot>
      </table>
    </div>
  );
};

export default BasicTable;
