          var headerLevel = [];
          /* Create map to storage PN of each level */
          const map_lv_PN = new Map();
          for (var i = 1; i <= maxLevel; i++) {
            levelArr.push(i);
            headerLevel.push("PN Parent Level " + i);
            map_lv_PN.set(i, "");
          }
          console.log("\n--- levelArr ---");
          console.log(levelArr);
          console.log("\n--- headerLevel ---");
          console.log(headerLevel);

          var currentLevel = 0;
          var currentPN = "";
          var currentLine = 0;
          var row_level;

          allRows.forEach((row, index) => {

            let copiedRow = JSON.parse(JSON.stringify(row));
            let row_level_string = copiedRow[col_A];
            let row_PN = copiedRow[col_C];
            /* Parse row_level_string to integer, return value 0 if it null, undefined, or string cant be parse*/
            row_level = isNaN(parseInt(row_level_string)) ? 0 : parseInt(row_level_string);

            levelArr.forEach((level) => {
              if (level === row_level) {
                /* If current row level === one level (example level 3)
                - update current level mapping = 3 (0)
                - keep the PN values of map before level 3,
                - change the PN value of level 3 by row[PN], (1)
                - then set the PN value after level 3 to EMPTY (2) */

                /* row_level must be next natural integer number of currentLevel */
                if ((row_level - currentLevel) >= 2) {
                  var informErr = "Line " + (index + 2) + ": Level (" + row_level + ") must be the NEXT integer number of parent level (" + currentLevel + ") (PN: " + currentPN + ") (Line: " + currentLine + ")";
                  console.log(informErr);
                  message.error(informErr);
                  var output_Err = {
                    "Line": (index + 2),
                    "Level": row_level,
                    "Part Number": copiedRow[col_C],
                    "Error": informErr
                  }
                  errorsArrStr.push(output_Err);
                } else {
                  currentLevel = row_level;
                  currentPN = row_PN;
                  currentLine = index + 2;
                }

                map_lv_PN.set(row_level, row[col_C]); // (1)
                for (var lv = row_level + 1; lv <= maxLevel; lv++) {
                  map_lv_PN.set(lv, "");// (2)
                }
                return;
              }
            });

            /* Adding columns headerLevel into results row*/
            for (var lvl = 1; lvl <= maxLevel; lvl++) {
              // copiedRow[headerLevel[lvl - 1]] = map_lv_PN.get(lvl);
              copiedRow = { ...copiedRow, [headerLevel[lvl - 1]]: map_lv_PN.get(lvl) };
            }
            results.push(copiedRow);
          });
