  const get_col_cells_of_worksheet = (worksheet) => {
    const cols = [];
    const range = utils.decode_range(worksheet['!ref']);
    let C;
    let R; /* start in the first row */
    var col_cells = [];
    for (C = range.s.c; C <= range.e.c; ++C) { /* walk every column in the range */
      col_cells = [];
      for (R = range.s.r; R <= range.e.r; ++R) { /* walk every row in the range (except header) */
        var cell = worksheet[utils.encode_cell({ c: C, r: R })]; /* find the cell in the first row */
        col_cells.push(cell);
      }
      cols.push(col_cells)
    }
    return cols;
  };

  // input result json data, return array of max-width data in result json
  const autofitColumns = (json, worksheet) => {
    let objectMaxLength = [];

    json.map(jsonData => {
      Object.entries(jsonData)
        .map(([header, v], idx) => {

          let columnValue = v;
          if (columnValue == null) {
            columnValue = "";
          }
          if (header != null && header.toString().length > columnValue.toString().length) {
            columnValue = header;
          }
          objectMaxLength[idx] = objectMaxLength[idx] >= columnValue.toString().length ? objectMaxLength[idx] : columnValue.toString().length;
          return [header, v];
        });
      return jsonData;
    });
    var padding = 5;
    var colWidth;
    const wscols = objectMaxLength.map((w) => {
      colWidth = 16.5;
      if (w !== 0) {
        colWidth = w + padding;
      }
      if (colWidth > 35) {
        colWidth = 35;
      }
      return { width: colWidth };
    });
    console.log("\n--- wscols ---");
    console.log(wscols);
    worksheet["!cols"] = wscols;
  }

